.App {
  background: linear-gradient(to bottom right, #ba2727 0%, #ed8bbc 100%); }

.day {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  page-break-inside: avoid;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  margin-bottom: 50px; }

.day-title {
  position: -webkit-sticky;
  position: sticky;
  top: 55px;
  z-index: 10;
  height: 50px;
  width: 400px;
  max-width: 100%;
  background: white;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.6);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

.day-title .text {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  white-space: nowrap; }

.card-list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
  -webkit-align-items: stretch;
          align-items: stretch; }

.card h5 {
  background: rgba(255, 255, 255, 0.6);
  margin-top: 0;
  padding: 20px;
  border-radius: 7px 7px 0 0; }

.day-title .itinerary-button {
  margin-left: auto;
  margin-right: 5px; }

i.fas {
  margin-right: 10px; }

.media {
  height: 200px; }

.activity {
  min-width: 300px;
  max-width: 600px;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 10px;
  margin-top: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

.actions {
  margin-top: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }

@media (min-width: 600px) {
  .activity {
    max-width: 400px; }
  .day-title {
    width: 400px;
    top: 63px; } }
